import React from "react";
import { Button } from "../Components/ui/button";
import { useTable } from "@/MonitoringService/Context/TableContext";

const fieldConfigs = {
  name: {
    header: "Name",
    render: (value) => <span className="font-medium">{value}</span>,
  },
  address: {
    header: "Address",
    render: (value) => <span className="text-gray-500">{value}</span>,
  },
  priority: {
    header: "Priority",
    render: (value, row, actions) => (
      <span
        onClick={() => {
          console.log("Clicked priority", row);
          actions?.fetchData?.();
        }}
        className={
          value === "High"
            ? "text-red-500 cursor-pointer"
            : value === "Medium"
            ? "text-orange-400 cursor-pointer"
            : "text-green-500 cursor-pointer"
        }
      >
        {value}
      </span>
    ),
  },
  action: {
    header: "Action",
    render: (value, row, actions) => (
      <Button
        size="sm"
        onClick={() => {
          actions?.fetchData?.();
          actions?.deleteRow?.(row);
        }}
      >
        View Profile
      </Button>
    ),
  },
};

const entityFields = {
  customer: ["name", "address", "priority", "action"],
  office: ["name", "address", "action"],
  distributor: ["name", "address", "action"],
};

export function useColumns(entityName) {
  const { actions } = useTable();

  const keys = entityFields[entityName] || [];
  return keys.map((key) => {
    const field = fieldConfigs[key];
    return {
      key,
      header: field.header,
      render: (value, row) => field.render(value, row, actions),
    };
  });
}
