import React, { useEffect, useCallback, useMemo } from "react";
import CardUI from "../common/card";
import TableUI from "../common/table";
import { Button } from "../ui/button";
import {
  TableProvider,
  useTable,
} from "@/MonitoringService/Context/TableContext";
import { useColumns } from "@/MonitoringService/Utiles/columnsUtil";

export default function CustomerTable() {
  const { page, setPage, actions, setActions } = useTable();
  const columns = useColumns("customer");
  const fetchData = useCallback(() => {
    console.log("ðŸ“¡ Fetch API with page:", page);
  }, [page]);

  const deleteRow = useCallback(
    (row) => {
      console.log("ðŸ—‘ï¸ Deleting row:", row);
      fetchData();
    },
    [fetchData]
  );

  useEffect(() => {
    setActions({
      fetchData,
      deleteRow,
    });
  }, [fetchData, deleteRow, setActions]);

  const data = [
    { id: 1, name: "Eleanor Alice", address: "456 Elm St", priority: "High" },
    { id: 2, name: "John Smith", address: "12 Pine Ave", priority: "Average" },
  ];

  useEffect(() => {
    fetchData();
  }, [page, fetchData]);

  return (
    <CardUI
      variant="noborder"
      title={<div className="text-xl font-bold">Hi there</div>}
      className="mb-7"
    >
      <div className="bg-background/50">
        <TableUI
          columns={columns}
          data={data}
          isPagination
          total={200}
          limit={15}
          headerClassName="bg-background/60"
          headerTextClassName="text-text/60"
          rowClassName=""
          //   cellClassName="text-gray-200"
        />
      </div>
    </CardUI>
  );
}
