import React, { useEffect, useState } from "react";
import { useLocation, Link } from "react-router-dom";
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
  BreadcrumbEllipsis,
} from "../../ui/breadcrumb";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "../../ui/dropdown-menu";
import { Drawer, DrawerContent, DrawerTrigger } from "../../ui/drawer";
import { useMediaQuery } from "@/hooks/use-media-query";

const LABEL_MAP = {
  ms: "Home",
  dashboard: "Dashboard",
  customer: "Customers",
  active: "Active Customers",
  inactive: "Inactive Customers",
  settings: "Settings",
  theme: "Theme",
};

const isMongoId = (segment) => /^[a-f\d]{24}$/i.test(segment);

const ITEMS_TO_DISPLAY = 3;

export default function BreadcrumbUI() {
  const location = useLocation();
  const isDesktop = useMediaQuery("(min-width: 768px)");
  const [items, setItems] = useState([]);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    const segments = location.pathname.split("/").filter(Boolean);

    const mapped = segments.map((segment, index) => {
      const path = "/" + segments.slice(0, index + 1).join("/");
      let label = LABEL_MAP[segment] || segment;

      if (segment === "ms") label = "Home";

      if (isMongoId(segment)) {
        const name = localStorage.getItem(`name-${segment}`);
        label = name || "Maj";
      } else if (!LABEL_MAP[segment]) {
        label = segment.charAt(0).toUpperCase() + segment.slice(1);
      }

      return { href: path, label };
    });

    // collapse /ms/dashboard â†’ just Home
    const normalized = mapped.filter((item, idx, arr) => {
      if (
        idx > 0 &&
        item.label === "Dashboard" &&
        arr[idx - 1].label === "Home"
      ) {
        return false;
      }
      return true;
    });

    setItems(normalized);
  }, [location]);

  const renderItems = () => {
    if (items.length <= ITEMS_TO_DISPLAY) {
      return items.map((item, index) => (
        <BreadcrumbItem key={index}>
          {index !== items.length - 1 ? (
            <>
              <BreadcrumbLink asChild>
                <Link to={item.href}>{item.label}</Link>
              </BreadcrumbLink>
              <BreadcrumbSeparator />
            </>
          ) : (
            <BreadcrumbPage>{item.label}</BreadcrumbPage>
          )}
        </BreadcrumbItem>
      ));
    }

    const first = items[0];
    const middle = items.slice(1, -1);
    const last = items[items.length - 1];

    return (
      <>
        {/* First item (always show Home) */}
        <BreadcrumbItem>
          <BreadcrumbLink asChild>
            <Link to={first.href}>{first.label}</Link>
          </BreadcrumbLink>
          <BreadcrumbSeparator />
        </BreadcrumbItem>

        {/* Ellipsis in the middle */}
        <BreadcrumbItem>
          {isDesktop ? (
            <DropdownMenu open={open} onOpenChange={setOpen}>
              <DropdownMenuTrigger className="flex items-center gap-1">
                <BreadcrumbEllipsis className="size-4" />
              </DropdownMenuTrigger>
              <DropdownMenuContent align="start">
                {middle.map((item, index) => (
                  <DropdownMenuItem key={index}>
                    <Link to={item.href}>{item.label}</Link>
                  </DropdownMenuItem>
                ))}
              </DropdownMenuContent>
            </DropdownMenu>
          ) : (
            <Drawer open={open} onOpenChange={setOpen}>
              <DrawerTrigger>
                <BreadcrumbEllipsis className="h-4 w-4" />
              </DrawerTrigger>
              <DrawerContent>
                <div className="grid gap-1 px-4">
                  {middle.map((item, index) => (
                    <Link key={index} to={item.href} className="py-1 text-sm">
                      {item.label}
                    </Link>
                  ))}
                </div>
              </DrawerContent>
            </Drawer>
          )}
        </BreadcrumbItem>
        <BreadcrumbSeparator />

        {/* Last item (always current page) */}
        <BreadcrumbItem>
          <BreadcrumbPage>{last.label}</BreadcrumbPage>
        </BreadcrumbItem>
      </>
    );
  };

  return (
    <Breadcrumb className="">
      <BreadcrumbList>{renderItems()}</BreadcrumbList>
    </Breadcrumb>
  );
}
