import React from "react";
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from "../../ui/table";
import { useTable } from "../../../Context/TableContext";
import { TablePagination } from "./TablePagination";

export default function TableUI({
  columns = [],
  data = [],
  isPagination = false,
  total = 0,
  limit = 10,

  // ðŸ”¹ className props for customization
  headerClassName = "bg-background/60",
  headerTextClassName = "text-text/60",
  rowClassName = "",
  cellClassName = "",
}) {
  const { triggerAction } = useTable();

  return (
    <div className="w-full">
      {/* Table */}
      <Table>
        <TableHeader className={headerClassName}>
          <TableRow>
            {columns.map((col) => (
              <TableHead
                key={col.key}
                className={`${headerTextClassName} font-medium`}
              >
                {col.header}
              </TableHead>
            ))}
          </TableRow>
        </TableHeader>

        <TableBody>
          {data.map((row, rowIndex) => (
            <TableRow key={rowIndex} className={rowClassName}>
              {columns.map((col) => (
                <TableCell key={col.key} className={cellClassName}>
                  {typeof col.render === "function"
                    ? col.render(row[col.key], row, rowIndex, () =>
                        triggerAction("cellClick", { col, row })
                      )
                    : row[col.key]}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {/* Pagination */}
      {isPagination && <TablePagination total={total} limit={limit} />}
    </div>
  );
}
